
Template.__checkName("fAdminLayout");
Template["fAdminLayout"] = new Template("Template.fAdminLayout", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("AdminConfig"));
  }, function() {
    return [ "\n  ", Blaze.If(function() {
      return Spacebars.dataMustache(view.lookup("isInRole"), "admin");
    }, function() {
      return [ "\n    ", HTML.DIV({
        "class": "admin-layout"
      }, "\n    ", Blaze._TemplateWith(function() {
        return {
          skin: Spacebars.call(view.lookup("admin_skin"))
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("AdminLTE"), function() {
          return [ "\n  			", Spacebars.include(view.lookupTemplate("AdminHeader")), "\n  			", Spacebars.include(view.lookupTemplate("AdminSidebar")), "\n				", HTML.DIV({
            "class": "content-wrapper",
            style: function() {
              return [ "min-height: ", Spacebars.mustache(view.lookup("minHeight")) ];
            }
          }, "\n					", HTML.SECTION({
            "class": "content-header"
          }, "\n						", HTML.H1("\n							", Blaze.View("lookup:$.Session.get", function() {
            return Spacebars.mustache(Spacebars.dot(view.lookup("$"), "Session", "get"), "admin_title");
          }), "\n							", HTML.SMALL(Blaze.View("lookup:$.Session.get", function() {
            return Spacebars.mustache(Spacebars.dot(view.lookup("$"), "Session", "get"), "admin_subtitle");
          })), "\n						"), "\n						", HTML.BR(), "\n						", HTML.OL({
            "class": "breadcrumb"
          }, "\n							", HTML.LI(HTML.A({
            href: "/admin/"
          }, "Dashboard")), "\n							", Blaze.If(function() {
            return Spacebars.dataMustache(Spacebars.dot(view.lookup("$"), "Session", "get"), "admin_collection_name");
          }, function() {
            return [ "\n							", HTML.LI(HTML.A({
              href: function() {
                return [ "/admin/view/", Spacebars.mustache(Spacebars.dot(view.lookup("$"), "Session", "get"), "admin_collection_name"), "/" ];
              }
            }, Blaze.View("lookup:adminCollectionLabel", function() {
              return Spacebars.mustache(view.lookup("adminCollectionLabel"), view.lookup("admin_collection_name"));
            }))), "\n							" ];
          }), "\n							", Blaze.If(function() {
            return Spacebars.dataMustache(Spacebars.dot(view.lookup("$"), "Session", "equals"), "admin_collection_page", "new");
          }, function() {
            return [ "\n							", HTML.LI("New"), "\n							" ];
          }), "\n							", Blaze.If(function() {
            return Spacebars.dataMustache(Spacebars.dot(view.lookup("$"), "Session", "equals"), "admin_collection_page", "edit");
          }, function() {
            return [ "\n							", HTML.LI("Edit"), "\n							" ];
          }), "\n						"), "\n					"), "\n					", HTML.SECTION({
            "class": "content"
          }, "\n						", Blaze._TemplateWith(function() {
            return {
              template: Spacebars.call(view.lookup("main"))
            };
          }, function() {
            return Spacebars.include(function() {
              return Spacebars.call(Template.__dynamic);
            });
          }), "\n					"), "\n				"), "\n    " ];
        });
      }), "\n    "), "\n	" ];
    }, function() {
      return [ "\n	", Spacebars.include(view.lookupTemplate("NotAdmin")), "\n	" ];
    }), "\n	" ];
  }, function() {
    return [ "\n	", Spacebars.include(view.lookupTemplate("NoConfig")), "\n	" ];
  });
}));

Template.__checkName("NotAdmin");
Template["NotAdmin"] = new Template("Template.NotAdmin", (function() {
  var view = this;
  return HTML.Raw('<div class="container">\n<div class="row">\n	<div class="col-md-4 col-md-offset-4">\n		<p class="alert alert-info" style="margin-top:100px;">\n			You need to be an admin to view this page\n		</p>\n	</div>\n</div>\n</div>');
}));

Template.__checkName("AdminLoading");
Template["AdminLoading"] = new Template("Template.AdminLoading", (function() {
  var view = this;
  return HTML.Raw("<p> Loading....</p>");
}));

Template.__checkName("NoConfig");
Template["NoConfig"] = new Template("Template.NoConfig", (function() {
  var view = this;
  return HTML.Raw("<p class=\"alert alert-info\">\n		You need to define an AdminConfig object to use the admin dashboard.\n		<br>\n		A basic config to manage the 'Posts' and 'Comments' collection would look like this:\n		<br>\n		<code>\n		AdminConfig = {\n		<br>\n			adminEmails: ['	ben@code2create.com'],\n		<br>\n			collections:\n		<br>\n				{\n		<br>\n					Posts: {},\n		<br>\n					Comments: {}\n		<br>\n				}\n		<br>\n		}\n		</code>\n	</p>");
}));
