
Template.__checkName("adminDefaultWidgets");
Template["adminDefaultWidgets"] = new Template("Template.adminDefaultWidgets", (function() {
  var view = this;
  return Blaze.Each(function() {
    return Spacebars.call(view.lookup("admin_collections"));
  }, function() {
    return [ "\n		", Blaze.Unless(function() {
      return Spacebars.dataMustache(view.lookup("$eq"), view.lookup("showWidget"), false);
    }, function() {
      return [ "\n			", Blaze._TemplateWith(function() {
        return {
          collection: Spacebars.call(view.lookup("name"))
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("adminCollectionWidget"));
      }), "\n		" ];
    }), "\n	" ];
  });
}));

Template.__checkName("adminCollectionWidget");
Template["adminCollectionWidget"] = new Template("Template.adminCollectionWidget", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return Blaze.If(function() {
        return Spacebars.call(view.lookup("class"));
      }, function() {
        return Blaze.View("lookup:class", function() {
          return Spacebars.mustache(view.lookup("class"));
        });
      }, function() {
        return "col-lg-3 col-xs-6";
      });
    }
  }, "\n	", Spacebars.With(function() {
    return Spacebars.dataMustache(view.lookup("adminGetCollection"), view.lookup("collection"));
  }, function() {
    return [ "\n		", HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("adminViewPath"), Spacebars.dot(view.lookup("."), "name"));
      }
    }, "\n			", HTML.DIV({
      "class": function() {
        return [ "small-box bg-", Spacebars.mustache(view.lookup("color")) ];
      }
    }, "\n				", HTML.DIV({
      "class": "inner"
    }, "\n					", HTML.H3("\n						", Blaze.View("lookup:adminCollectionCount", function() {
      return Spacebars.mustache(view.lookup("adminCollectionCount"), view.lookup("name"));
    }), "\n					"), "\n					", HTML.P("\n						", Blaze.View("lookup:..label", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "label"));
    }), "\n					"), "\n				"), "\n				", HTML.DIV({
      "class": "icon"
    }, "\n					", HTML.I({
      "class": function() {
        return [ "fa fa-", Spacebars.mustache(Spacebars.dot(view.lookup("."), "icon")) ];
      }
    }), "\n				"), "\n				", HTML.A({
      "class": "small-box-footer"
    }, "\n					See all ", HTML.I({
      "class": "fa fa-arrow-circle-right"
    }), "\n				"), "\n			"), "\n		"), "\n		" ];
  }), "\n	");
}));

Template.__checkName("adminDeleteWidget");
Template["adminDeleteWidget"] = new Template("Template.adminDeleteWidget", (function() {
  var view = this;
  return HTML.Raw('<div class="alert alert-warning admin-alert">\n		<h4>Confirm delete</h4>\n		<div>\n			<p>Are you sure you want to delete this document ? </p>\n		</div>\n		<div>\n			<button type="button" id="confirm-delete" class="btn btn-danger">Delete</button>\n		</div>\n	</div>');
}));
