
Template.__checkName("AdminHeader");
Template["AdminHeader"] = new Template("Template.AdminHeader", (function() {
  var view = this;
  return HTML.HEADER({
    "class": "main-header"
  }, "\n        ", HTML.A({
    href: "/admin",
    "class": "logo"
  }, "\n            ", Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("AdminConfig"), "name"));
  }, function() {
    return [ "\n            ", Blaze.View("lookup:AdminConfig.name", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("AdminConfig"), "name"));
    }), "\n            " ];
  }, function() {
    return "\n            Flow-DB-Admin\n            ";
  }), "\n        "), "\n        ", HTML.NAV({
    "class": "navbar navbar-static-top",
    role: "navigation"
  }, "\n            ", HTML.Raw("<!-- Sidebar toggle button-->"), "\n            ", HTML.Raw('<a href="#" class="sidebar-toggle" data-toggle="offcanvas" role="button">\n                <span class="sr-only">Toggle navigation</span>\n                <span class="icon-bar"></span>\n                <span class="icon-bar"></span>\n                <span class="icon-bar"></span>\n            </a>'), "\n            ", HTML.DIV({
    "class": "navbar-custom-menu"
  }, "\n                ", HTML.UL({
    "class": "nav navbar-nav"
  }, "\n                    ", HTML.LI("\n                        ", Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("AdminConfig"), "dashboard", "homeUrl"));
  }, function() {
    return [ "\n                        ", HTML.A({
      href: function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("AdminConfig"), "dashboard", "homeUrl"));
      }
    }, "Home"), "\n                        " ];
  }, function() {
    return [ "\n                        ", HTML.A({
      href: "/"
    }, "Home"), "\n                        " ];
  }), "\n\n                    "), "\n                    ", HTML.LI({
    "class": "dropdown"
  }, "\n                        ", HTML.Raw('<a href="#" class="dropdown-toggle" data-toggle="dropdown">\n                            <span>Admin<i class="caret"></i></span>\n                        </a>'), "\n                        ", HTML.UL({
    "class": "dropdown-menu"
  }, "\n                            ", HTML.LI({
    "class": ""
  }, "\n                            ", HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("profilepath"));
    }
  }, "\n                                Your profile\n                                "), "\n                            "), "\n                            ", HTML.Raw('<li class="">\n                                <a href="#" class="btn-sign-out">Sign out\n                                </a>\n                            </li>'), "\n                        "), "\n                    "), "\n                "), "\n            "), "\n        "), "\n    ");
}));
