
Template.__checkName("AdminSidebar");
Template["AdminSidebar"] = new Template("Template.AdminSidebar", (function() {
  var view = this;
  return HTML.ASIDE({
    "class": "main-sidebar"
  }, "\n		", HTML.DIV({
    "class": "sidebar"
  }, "\n			", HTML.UL({
    "class": "sidebar-menu"
  }, "\n				", HTML.LI({
    "class": function() {
      return Spacebars.mustache(view.lookup("isActiveRoute"), "adminDashboard");
    }
  }, "\n					", HTML.Raw('<a href="/admin">\n						<i class="fa fa-dashboard"></i> <span>Dashboard</span>\n					</a>'), "\n				"), "\n				", Blaze.Each(function() {
    return Spacebars.call(view.lookup("admin_collections"));
  }, function() {
    return [ "\n				", HTML.LI({
      "class": "treeview"
    }, "\n					", HTML.A({
      href: "#"
    }, "\n						", HTML.I({
      "class": function() {
        return [ "fa fa-", Spacebars.mustache(Spacebars.dot(view.lookup("."), "icon")) ];
      }
    }), "\n						", HTML.SPAN(Blaze.View("lookup:..label", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "label"));
    })), "\n						", HTML.I({
      "class": "fa fa-angle-left pull-right"
    }), "\n					"), "\n					", HTML.UL({
      "class": "treeview-menu"
    }, "\n						", HTML.LI({
      "class": function() {
        return Spacebars.mustache(view.lookup("isActivePath"), view.lookup("newPath"));
      }
    }, HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("newPath"));
      }
    }, HTML.I({
      "class": "fa fa-angle-double-right"
    }), " New")), "\n						", HTML.LI({
      "class": function() {
        return Spacebars.mustache(view.lookup("isActivePath"), view.lookup("viewPath"));
      }
    }, HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("viewPath"));
      }
    }, HTML.I({
      "class": "fa fa-angle-double-right"
    }), " View All")), "\n						", Blaze.Each(function() {
      return Spacebars.call(view.lookup("admin_collection_items"));
    }, function() {
      return [ "\n						", HTML.LI({
        "class": function() {
          return Spacebars.mustache(view.lookup("isActivePath"), view.lookup("url"));
        }
      }, HTML.A({
        href: function() {
          return Spacebars.mustache(view.lookup("url"));
        }
      }, HTML.I({
        "class": "fa fa-angle-double-right"
      }), " ", Blaze.View("lookup:title", function() {
        return Spacebars.mustache(view.lookup("title"));
      }))), "\n						" ];
    }), "\n					"), "\n				"), "\n				" ];
  }), "\n				", Blaze.Each(function() {
    return Spacebars.call(view.lookup("admin_sidebar_items"));
  }, function() {
    return [ "\n					", Blaze.If(function() {
      return Spacebars.call(Spacebars.dot(view.lookup("options"), "urls"));
    }, function() {
      return [ "\n						", Spacebars.include(view.lookupTemplate("adminSidebarItemTree")), " -->\n					" ];
    }, function() {
      return [ "\n						", Spacebars.include(view.lookupTemplate("adminSidebarItem")), " -->\n					" ];
    }), "\n				" ];
  }), "\n			"), "\n		"), "\n	");
}));

Template.__checkName("adminSidebarItem");
Template["adminSidebarItem"] = new Template("Template.adminSidebarItem", (function() {
  var view = this;
  return HTML.LI({
    "class": function() {
      return Spacebars.mustache(view.lookup("isActivePath"), view.lookup("url"));
    }
  }, "\n		", HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("url"));
    }
  }, "\n			", Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("options"), "icon"));
  }, function() {
    return [ "\n			", HTML.I({
      "class": function() {
        return [ "fa fa-", Spacebars.mustache(Spacebars.dot(view.lookup("options"), "icon")) ];
      }
    }), "\n			" ];
  }), "\n			", HTML.SPAN(Blaze.View("lookup:title", function() {
    return Spacebars.mustache(view.lookup("title"));
  })), "\n		"), "\n	");
}));

Template.__checkName("adminSidebarItemTree");
Template["adminSidebarItemTree"] = new Template("Template.adminSidebarItemTree", (function() {
  var view = this;
  return HTML.LI({
    "class": "treeview"
  }, "\n		", HTML.A({
    href: "#"
  }, "\n			", Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("options"), "icon"));
  }, function() {
    return [ "\n			", HTML.I({
      "class": function() {
        return [ "fa fa-", Spacebars.mustache(Spacebars.dot(view.lookup("options"), "icon")) ];
      }
    }), "\n			" ];
  }), "\n			", HTML.SPAN(Blaze.View("lookup:title", function() {
    return Spacebars.mustache(view.lookup("title"));
  })), "\n			", HTML.Raw('<i class="fa fa-angle-left pull-right"></i>'), "\n		"), "\n		", HTML.UL({
    "class": "treeview-menu"
  }, "\n			", Blaze.Each(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("options"), "urls"));
  }, function() {
    return [ "\n			", HTML.LI({
      "class": function() {
        return Spacebars.mustache(view.lookup("isActivePath"), view.lookup("url"));
      }
    }, HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("url"));
      }
    }, HTML.I({
      "class": "fa fa-angle-double-right"
    }), Blaze.View("lookup:title", function() {
      return Spacebars.mustache(view.lookup("title"));
    }))), "\n			" ];
  }), "\n		"), "\n	");
}));
